<template>
  <div class="notfound">
    <div class="row ohthehorror valign-wrapper">
      <div class="col s12">
        <div class="center-align">
          <span class="icon-horror"></span>
        </div>
        <h2 class="center-align">
          {{ msg }}
        </h2>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'globalFooter',
    data () {
      return {
        msg: 'Sorry, the page you requested does not exist.'
      }
    },
    methods: {
      getPic(value) {
        let imgs = require.context('../assets/img', false, /\.png$/);
        return imgs('./' + value);
      }
    }
  }
</script>

<style scoped>
  .ohthehorror {
    min-height: 400px;
  }
  .icon-horror {
    font-size: 10rem;
  }
</style>


