<template>
  <div class="post-review">
    <h2>
      Add a Review
    </h2>
    <form>
      <div class="input-field">
        <v-text-input name="title" id="title" v-model="title"></v-text-input>
        <label for="first">Title</label>
      </div>
      <div class="input-field">
        <v-select name="topic"
                  id="topic"
                  v-model="topic"
                  :items="topics"
        ></v-select>
        <label for="select">Topic</label>
      </div>
      <div class="input-field">
        <v-text-input name="videoLink"
                      id="videoLink"
                      v-model="videoLink"
        ></v-text-input>
        <label for="first">Video Link</label>
      </div>
      <label for="input-field">
        Main Image
      </label>
      <div class="input-field">
        <v-text-input name="mainImage" id="mainImage" v-model="mainImage"></v-text-input>
        <label for="mainImage">Main Image</label>
      </div>
      <label for="vue-editor">
        Review
      </label>
      <quill-editor v-model="content"
                    ref="myQuillEditor"
                    :options="editorOption"
                    @change="onEditorChange($event)">
      </quill-editor>
      <div class="row">
        <v-btn large waves-effect><span v-on:click="addReview">Add Review</span></v-btn>
      </div>
    </form>
  </div>
</template>

<script>
  import * as services from '../services';
  export default {
    data () {
      return {
        topic: '',
        title: '',
        article: '',
        author: '',
        videoLink: '',
        mainImage: '',
        review: '',
        error: '',
        topics: [
          {
            id: 'anime',
            text: 'Anime'
          }, {
            id: 'tech',
            text: 'Tech'
          }, {
            id: 'zombies',
            text: 'Zombies'
          }, {
            id: 'horror',
            text: 'Horror'
          }, {
            id: 'fantasy',
            text: 'Fantasy'
          }, {
            id: 'scifi',
            text: 'Scifi'
          }, {
            id: 'superheroes',
            text: 'Superheroes'
          }, {
            id: 'games',
            text: 'Games'
          }, {
            id: 'litrpg',
            text: 'LitRPG'
          }
        ]
      }
    },
    computed: {
      editor() {
        return this.$refs.myQuillEditor.quill
      }
    },
    mounted() {
      console.log('this is current quill instance object', this.editor)
    },
    methods: {
      onEditorChange({ editor, html, text }) {
        console.log('editor change!', editor, html, text)
        this.article = html;
        console.log('article', this.article);
      },
      addReview() {
        console.log('adding');
        services.app.authenticate().then(result => {
          console.log('auth result', result.data);
          let reviewObj = {
            topic: this.topic,
            title: this.title,
            article: this.article,
            author: result.data._id,
            videoLink: this.videoLink,
            mainImage: this.mainImage
          };
          console.log('Obj', reviewObj);
          if (result.data._id) {
            services.reviewService.create(reviewObj)
              .then(this.topic = '')
              .then(this.title = '')
              .then(this.article = '')
              .then(this.author = '')
              .then(this.videoLink = '')
              .then(this.mainImage = '')
              .then(this.successMessage = 'Article created!')
              .catch(function (err) {
                console.log('Uh oh, something bad happened!', err);
              })
          }
        })
      }
    }
  }
</script>


