<template>
  <div class="goodreads">
    <v-card panel class="blue-grey darken-2 no-top">
        <span class="white-text">
            <v-icon tiny>info</v-icon> Want to see what we've read? Take a gander below at the latest two hundred novels pulled
        directly from GoodReads.
        </span>
    </v-card>
    <div id="gr_grid_widget_1493014219">
      <div class="loading center-align" v-if="loading">
        <v-progress-circular active large></v-progress-circular>
      </div>
    </div>
  </div>
</template>

</div>
<script>
  import loadScript from 'load-script';
  export default {
    data() {
      return {
        loading: true
      }
    },
    created() {
      this.getData();
    },
    methods: {
      getData() {
        const source = "https://www.goodreads.com/review/grid_widget/572287.UGR's%20bookshelf?cover_size=medium&hide_link=&hide_title=&num_books=200&order=d&shelf=read&sort=date_added&widget_id=1493014219";
        loadScript(source, function(err, script) {
          if (err) {
            console.log('err', err)
          }
          if (script) {
            console.log('script', script);
            this.loading = false;
          }
        })
      }
    }
  }
</script>

<style type="text/css" media="screen">
  .gr_grid_container {
    /* customize grid container div here. eg: width: 500px; */
  }

  .gr_grid_book_container {
    /* customize book cover container div here */
    float: left;
    width: 98px;
    height: 160px;
    padding: 0px 0px;
    overflow: hidden;
  }
</style>


