<template>
  <div class="global-footer">
    <v-footer class="blue-grey darken-1">
      <div class="container" slot="content">
        <div class="row">
          <div class="col s3">
            <div class="valign-wrapper center-align imgholder">
              <img class="responsive-image" :src="getPic('skull-footer.png')">
            </div>
          </div>
          <div class="col s4">
            <h5 class="white-text">
              Links
            </h5>
            <ul>
              <li>
                <router-link to="/media">Media</router-link>
              </li>
              <li>
                <router-link to="/services">Services</router-link>
              </li>
              <li>
                <router-link to="/contact">Contact Us</router-link>
              </li>
            </ul>
          </div>
          <div class="col s4">
            <h5 class="white-text">
              Topics
            </h5>
            <ul>
              <li v-for="item in items">
                <router-link :to="item.link">{{item.title}}</router-link>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="container" slot="copyright">
        <div class="left margin-40">
          &copy; Copyright 2017 UberGeek Reviews, a YokaiKick Company
        </div>
        <div class="right">
          <ul class="socialicons">
            <li>
              <a href="https://www.youtube.com/channel/UCh5H1QdjvP3nqLTGNGvUigA" target="_blank"><span><i class="mdi mdi-youtube-play"></i></span></a>
            </li>
            <li>
              <a href="https://twitter.com/UbergeekReviews" target="_blank"><span><i class="mdi mdi-twitter"></i></span></a>
            </li>
            <li>
              <a href="https://plus.google.com/u/3/b/105949299079878705057" target="_blank"><span><i class="mdi mdi-google"></i></span></a>
            </li>
            <li>
              <a href="#"><span><i class="mdi mdi-facebook"></i></span></a>
            </li>
          </ul>
        </div>
      </div>
    </v-footer>
  </div>
</template>

<script>
  export default {
    name: 'globalFooter',
    data () {
      return {
        items: this.$parent.items
      }
    },
    methods: {
      getPic(value) {
        let imgs = require.context('../assets/img', false, /\.png$/);
        return imgs('./' + value);
      }
    }
  }
</script>

<style>
  .imgholder {
    margin-top: 40px;
  }
  .margin-40 {
    margin-top: 40px;
  }
  .socialicons li {
    padding-right: 20px;
    display: inline;
  }
  .socialicons li i {
    font-size: 3rem;
  }
  @media only screen and (min-width: 993px) {
    .container { width: 90% !important; }
  }
</style>


